
<script>
export default {
  methods: {
    countMonthDays (year, month) {
      return /4|6|9|11/.test(month) ? 30 : month === 2 ? ((!(year % 4) && year % 100) || !(year % 400) ? 29 : 28) : 31
    },
    zellerCongruence (year, month, day) {
      // Ëî°ÂãíÂÖ¨Âºè‰∏≠ 1, 2 ÊúàË¶ñÁÇ∫Ââç‰∏ÄÂπ¥ÁöÑ 13, 14 Êúà
      if (month === 1 || month === 2) {
        month += 12
        year -= 1
      }
      const c = Math.floor(year / 100) // Âπ¥‰ªΩÂâçÂÖ©‰ΩçÊï∏
      const y = year % 100 // Âπ¥‰ªΩÂæåÂÖ©‰ΩçÊï∏
      const m = month
      const d = day
      let w = 0
      // TODO: 1582.10.15 ‰ª•ÂæåÊîπÊõÜÔºåÁõÆÂâçÂ∞öÊú™ËôïÁêÜ 1582.10.4 ‰ª•ÂâçÂÖ¨Âºè
      w = y + Math.floor(y / 4) + Math.floor(c / 4) - 2 * c + Math.floor((26 * (m + 1)) / 10) + d - 1
      if (w < 0) w = (w & (7 + 7)) % 7
      else w = w % 7
      return w
    }
  }
}
</script>
